import React, { Component } from "react";
import Button from "@mui/material/Button";
import {File} from "../File";
import {DragAndDrop} from "../DragAndDrop";
import { LoadingButton } from "@mui/lab";



import './ThirdStep.css';

export class ThirdStep  extends Component{
  state = {
    haveContract: false,
    doc: false,
    ext: false,
  }

  isHaveContract = () => {
    for (let document of this.props.documents) {
      if (document.documentType === 'contract') {
        this.setState({haveContract: true})
      }
    }
  }
  getNamePromo = () => {
    if (this.props.docType === '') {
      if (this.props.isSms === '') {
        return ''
      } else {
        return ''
      }
    } else {
      return ''
    }
  }
  getDocument = (req, name) => {
    console.log(this.state[name])
    console.log(name)
    const requestOptions = {
      method: 'POST',
      body: JSON.stringify(req)
    }
    this.setState({ [name]: true } );
    fetch('https://crm.centralnoe.ru/dealincom/templates/sk.php', requestOptions)
      .then(res => {
        res.text().then(url => {
          this.setState({ [name]: false } );
          if (url !== 'false'){
            location.href = url;
          }
        })
    })
  }
  render() {
    const { documents, docForm, docType, handleInputs, UID } = this.props;
    const namePromo = this.getNamePromo();

    return (
        <div className='container-page'>
          {
            docType === '' && docForm === '' ? '' :
            <span className='subtitle'> </span>
          }
          {
           docType === '' && docForm === '' ? '' :
          <div className='downloads'>
            {
              docType === '' ?
              <div className='downloads__item'>
                <span> </span>
                {/*<Button*/}
                {/*  variant="contained"*/}
                {/*  onClick={() => this.getDocument({packUID: UID})}*/}
                {/*>*/}
                {/*  */}
                {/*</Button>                */}
                <LoadingButton
                  name='doc'
                  variant="contained"
                  onClick={event => this.getDocument({packUID: UID}, event.target.name)}
                  loading={this.state.doc}
                  loadingIndicator="Loading..."
                >
                  
                </LoadingButton>
              </div>
                :
              <>
                <div className='downloads__item'>
                  <span> (+   )</span>
                  <LoadingButton
                    name="doc"
                    variant="contained"
                    onClick={event => this.getDocument({packUID: UID}, event.target.name)}
                    loading={this.state.doc}
                  >
                    
                  </LoadingButton>
                </div>
                <div className='downloads__item'>
                <span>   </span>
                <LoadingButton
                  name='ext'
                  variant="contained"
                  onClick={event => this.getDocument({packUID: UID, Ext: 1}, event.target.name)}
                  loading={this.state.ext}
                >
                
                </LoadingButton>
                </div>
              </>
            }
          </div>
          }
          <span className='subtitle'> {namePromo}</span>
          <div>
            <div className="upload">
              <DragAndDrop
                container={'contract'}
                title={` ${docType === '' ? namePromo : ' (+   )'}`}
                sendFiles={this.props.sendFiles}
              />
              {
                docType !== '' &&
                <>
                  <DragAndDrop
                    container={'other'}
                    title={``}
                    sendFiles={this.props.sendFiles}
                  />
                  <DragAndDrop
                    container={'egrn'}
                    title={` `}
                    sendFiles={this.props.sendFiles}
                  />
                </>
              }
            </div>
          </div>
          { documents.length > 0 &&
          <>
            <span className='subtitle'></span>
            <div className='documents'>
              {
                documents.map(document => <File key={document.UID} document={document}/>)
              }
            </div>
          </>
          }
          <Button
            disabled={!this.state.haveContract}
            name='step'
            value='4'
            onClick={(event) => handleInputs(event)}
            variant="contained"
            data-action='nextStep'
          >
              
          </Button>
      </div>
    );
  }

  componentDidMount() {
    this.isHaveContract();
  }
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (this.props.documents !== prevProps.documents) {
      this.isHaveContract();
    }
  }
}
