import React, { Component } from "react";
import moment from "moment";
import Button from "@mui/material/Button";

import './FourthStep.css';

import {Notice} from "../Notice";
import {File} from "../File";
import {DialogMain} from "../DialogMain";
import {DialogType} from "../DialogType";
import {DialogSMS} from "../DialogSMS";

export class FourthStep extends Component{
  state = {
    openChangeType: false,
    openSendSMS: false,
    titleDialog: ''
  }

  setOpenChangeType = () => {
    this.setState({openChangeType: !this.state.openChangeType});
  }
  setOpenSendSMS = () => {
    this.setState({openSendSMS: !this.state.openSendSMS});
  }

  setTitleDialog = (title) => {
    this.setState({titleDialog: title})
  }

  render() {
    const { docType, docForm, docExpired, documents, smsvalidation, repeatSendSMS, sendAlterObject,
      moderation, handleInputs, setNewType, docProlongation, clientsPhones, isRepeat, sendFiles, isGod } = this.props;
    return (<div className='container-page'>
      <div className='about'>
        <div className='about__wrap'>
          <span>: {docType}</span>
          { docType === '' &&
            <>
              { moderation.status !== '' ?
                <Button
                  variant="text"
                  name='step'
                  data-action='changeTypeFull'
                  value='1'
                  onClick={(event) => handleInputs(event)}
                >
                   
                </Button>
                :
                <Button
                  variant="text"
                  onClick={() => {this.setOpenChangeType(); this.setTitleDialog('  ')}}
                >
                   
                </Button>
              }
            </>
           }
        </div>
        {
          docType === '' && <div className='about__wrap'>: {docForm}</div>
        }
        <div className='about__wrap'>
          <span>: {moment(docType === '' && moderation.status !== '' ? docProlongation : docExpired).format('DD.MM.YYYY')}</span>
          { (docType === '' && moderation.status === '') &&
          <Button
            variant="text"
            onClick={() => {this.setOpenChangeType(); this.setTitleDialog(' ')}}
          >
            
          </Button> }
        </div>
        <div className='about__wrap'> : {moderation.status}  {moderation.date && moment(moderation.data).format('DD.MM.YYYY, HH:mm')}</div>
        {
          (docType === '' && docForm === '') &&
          <div className='about__wrap'>
            <span>
               : {smsvalidation.status} {smsvalidation.phone} {smsvalidation.date &&  moment(smsvalidation.date).format('DD.MM.YYYY, HH:mm')}
            </span>
            {
              (!isRepeat || isGod) &&
                <Button
                  variant="text"
                  onClick={this.setOpenSendSMS}
                >
                   
                </Button>
            }
          </div>
        }
      </div>
      { moderation.status === '' &&
      <Notice
        moderation={moderation}
        docType={docType}
        sendFiles={sendFiles}
        docExpired={docExpired}
        docProlongation={docProlongation}
        sendAlterObject={sendAlterObject}
        setNewType={setNewType}
      /> }
      {documents.length > 0 &&
      <>
        <span className='subtitle'></span>
        <div className='documents'>
          {
            documents.map(document => <File key={document.UID} document={document}/>)
          }
        </div>
      </>
      }
      <DialogMain
        open={this.state.openChangeType}
        onClose={this.setOpenChangeType}
        fullWidth={true}
        maxWidth={'xs'}
      >
        <DialogType
          onClose={this.setOpenChangeType}
          setNewType={setNewType}
          title={this.state.titleDialog}
        />
      </DialogMain>
      <DialogMain
        open={this.state.openSendSMS}
        onClose={this.setOpenSendSMS}
        fullWidth={true}
        maxWidth={'xs'}
      >
        <DialogSMS
          onClose={this.setOpenSendSMS}
          clientsPhones={clientsPhones}
          repeatSendSMS={repeatSendSMS}
        />
      </DialogMain>
    </div>)
  }
}