import React, {useState} from 'react';
import moment from 'moment'
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';

import './FirstStep.css';

export function FirstStep(props){
  const { docType, docExpired, docForm, rights } = props;
  const { handleInputs, setExpired } = props;

  const [isErrorDate, setIsErrorDate] = useState(false);
  const [textErrorDate, setTextErrorDate] = useState('');
  const [errorTypeText, setErrorTypeText] = useState('');
  const [errorViewText, setErrorViewText] = useState('');

  const isValid = (event) => {
    const validLibrary = {
      docType: true,
      docForm: true,
      docExpired: true,
    }
    if (docType === '' && new Date(docExpired) < new Date() || docExpired.length === 0){
      setIsErrorDate(true);
      setTextErrorDate('        ');
      validLibrary.docExpired = false;
    } else {
      validLibrary.docExpired = true;
      setIsErrorDate(false);
      setTextErrorDate('');
    }

    if (!docType){
      setErrorTypeText('  ');
      validLibrary.docType = false;
    } else {
      validLibrary.docType = true;
      setErrorTypeText('');
    }
    if (docType === '' && !docForm){
      setErrorViewText('  ');
      validLibrary.docForm = false;
    } else {
      validLibrary.docForm = true;
      setErrorViewText('');
    }

    if (validLibrary.docType && validLibrary.docForm && validLibrary.docExpired) {
      handleInputs(event);
    }
  }

  return (<div className='container-page'>
              <div className='container-page__wrap'>
                <span className='subtitle'>  </span>
                <div>
                <div className='toggle-btn'>
                  <div className='toggle-btn__item toggle-btn__item50'>
                    <input
                      id='ex'
                      name='docType'
                      type="radio"
                      checked={docType === ''}
                      value=''
                      onChange={(event) => {handleInputs(event); setExpired(event)}}
                    />
                    <label htmlFor='ex'>
                      
                    </label>
                  </div>
                  <div className='toggle-btn__item toggle-btn__item50'>
                    <input
                      id='promo'
                      name='docType'
                      type="radio"
                      checked={docType === ''}
                      value=''
                      onChange={(event) => {handleInputs(event); setExpired(event);}}
                    />
                    <label htmlFor='promo'>
                      
                    </label>
                  </div>
                </div>
                  <span className='error-text'>{errorTypeText}</span>
                </div>
              </div>
              <div className={`container-page__wrap ${docType === '' && 'inVisible'}`}>
                <span className='subtitle'>  </span>
                <div>
                <div className='toggle-btn'>
                  { rights !== 'sks' &&
                    <div className='toggle-btn__item'>
                      <input
                        id='paper'
                        name='docForm'
                        type="radio"
                        checked={docForm === ''}
                        value=''
                        onChange={(event) => handleInputs(event)}
                      />
                      <label htmlFor='paper'>
                        
                      </label>
                    </div>
                  }
                  <div className='toggle-btn__item'>
                    <input
                      id='sms'
                      name='docForm'
                      type="radio"
                      checked={docForm === ''}
                      value=''
                      onChange={(event) => handleInputs(event)}
                    />
                    <label htmlFor='sms'>
                      
                    </label>
                  </div>
                  { rights !== 'sks' &&
                    <div className='toggle-btn__item'>
                      <input
                        id='screen'
                        name='docForm'
                        type="radio"
                        checked={docForm === ''}
                        value=''
                        onChange={(event) => handleInputs(event)}
                      />
                      <label htmlFor='screen'>
                        
                      </label>
                    </div>
                  }
                  {/*{ rights !== 'owner' &&*/}
                  {/*  <div className='toggle-btn__item'>*/}
                  {/*    <input*/}
                  {/*      id='ring'*/}
                  {/*      name='docForm'*/}
                  {/*      type="radio"*/}
                  {/*      checked={docForm === ''}*/}
                  {/*      value=''*/}
                  {/*      onChange={(event) => handleInputs(event)}*/}
                  {/*    />*/}
                  {/*    <label htmlFor='ring'>*/}
                  {/*      */}
                  {/*    </label>*/}
                  {/*  </div>*/}
                  {/*}*/}
                </div>
                  <span className='error-text'>{ errorViewText }</span>
                </div>
              </div>
              {docType === '' &&
                <div className='container-page__wrap'>
                  <span className='subtitle'>   </span>
                  <TextField
                    error={isErrorDate}
                    id="date"
                    label="  "
                    type="date"
                    name='docExpired'
                    disabled={docType === ''}
                    value={ `${docExpired ? docExpired.split(' ')[0] : moment().format('YYYY-MM-DD')}` }
                    onChange={(event) => handleInputs(event)}
                    helperText={docType === '' ? '       90  ' : textErrorDate}
                    size="small"
                  />
                </div>
              }
              <Button
                name='step'
                value="2"
                onClick={(event) => isValid(event)}
                variant="contained"
                data-action='nextStep'
              >
                
              </Button>
            </div>)
}
